html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
article,
details,
section,
summary,
aside,
main,
menu,
nav,
figcaption,
figure,
footer,
header,
hgroup {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
b,
strong {
  font-weight: bold;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 1em 40px;
}
hr {
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type='button'],
input[type='reset'],
input[type='submit'] {
  cursor: pointer;
  -webkit-appearance: button;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type='checkbox'],
input[type='radio'] {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  height: auto;
}
input[type='search'] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}
input[type='search']::-webkit-search-cancel-button,
input[type='search']::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
html {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 16px;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
}
h1 {
  margin-top: 3rem;
}
h2 {
  margin-top: 2rem;
  margin-bottom: 0.5rem;
}
h2.muted {
  color: #646261;
}
a {
  color: #646261;
  text-decoration: none;
}
.period {
  font-size: 0.8rem;
  color: #838383;
}
.title {
  color: #646261;
  font-weight: 700;
}
@media only screen and (max-width: 400px) {
  html {
    font-size: 12px;
  }
}
@media only screen and (min-width: 401px) and (max-width: 480px) {
  html {
    font-size: 15px;
  }
}
@media only screen and (min-width: 481px) and (max-width: 580px) {
  html {
    font-size: 6px;
  }
}
@media only screen and (min-width: 581px) and (max-width: 680px) {
  html {
    font-size: 7px;
  }
}
@media only screen and (min-width: 681px) and (max-width: 720px) {
  html {
    font-size: 9px;
  }
}
@media only screen and (min-width: 721px) and (max-width: 860px) {
  html {
    font-size: 10px;
  }
}
@media only screen and (min-width: 861px) and (max-width: 920px) {
  html {
    font-size: 11px;
  }
}
@media only screen and (min-width: 921px) and (max-width: 1000px) {
  html {
    font-size: 12px;
  }
}
@media only screen and (min-width: 1001px) and (max-width: 1200px) {
  html {
    font-size: 13px;
  }
}
@media only screen and (min-width: 1200px) {
  html {
    font-size: 16px;
  }
}
ul.list {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul.list li:before {
  content: "\2666";
  margin-right: 10px;
}
.container {
  position: relative;
  height: 100vh;
  overflow: hidden;
  lost-utility: clearfix;
}
@media only screen and (max-width: 480px) {
  .container {
    padding-bottom: 250px;
    overflow: visible;
    height: auto;
  }
}
.container.fullscreen .sidebar {
  lost-column: 0/12 12 0;
  overflow: hidden;
}
.container.fullscreen .content {
  lost-column: 12/12 12 0;
}
.sidebar {
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
  lost-column: 4/12 12 0;
  height: 100vh;
  overflow: auto;
}
@media only screen and (max-width: 480px) {
  .sidebar {
    lost-column: 1/1 1 0;
    overflow: visible;
    height: auto;
  }
}
.content {
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
  lost-column: 8/12 12 0;
  height: 100vh;
  overflow: auto;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px 5%;
}
@media only screen and (max-width: 480px) {
  .content {
    lost-column: 1/1 1 0;
    overflow: visible;
    height: auto;
    margin-bottom: 25px;
  }
}
.cols-4,
.languages,
.projects {
  lost-utility: clearfix;
}
.cols-4 > *,
.languages > *,
.projects > * {
  lost-column: 1/4;
  max-width: 200px;
}
.map {
  lost-utility: clearfix;
}
.map img {
  lost-column: 1/1;
  max-width: 890px;
}
#sidebar-toggle img {
  width: 2rem;
  height: auto;
}
@media only screen and (max-width: 480px) {
  #sidebar-toggle {
    display: none;
  }
}
ul.skills {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul.skills li {
  display: inline-block;
  margin-right: 2%;
  margin-bottom: 1rem;
  padding: 0.7rem 0.7rem;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  background-color: #f0f0f0;
  border-radius: 2rem;
}
nav.nav-links {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #fafafa;
}
nav.nav-links a {
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  padding: 2rem;
  padding-left: calc(11% + 4rem);
  padding-right: 5px;
  color: #000;
  font-size: 1.2rem;
  font-weight: 700;
  border-bottom: 1px solid #dad9da;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
  opacity: 0.4;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
  filter: alpha(opacity=40);
}
nav.nav-links a:hover {
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
  opacity: 1;
  -ms-filter: none;
  filter: none;
  background-color: #f0f0f0;
}
@media only screen and (max-width: 480px) {
  nav.nav-links a {
    padding-left: calc(5% + 4rem);
  }
}
nav.nav-links a:before {
  content: "";
  position: absolute;
  left: 11%;
  top: 1.5rem;
  width: 3rem;
  height: 3rem;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
}
@media only screen and (max-width: 480px) {
  nav.nav-links a:before {
    left: 5%;
  }
}
nav.nav-links a.about:before {
  background-image: url("../img/about.png");
}
nav.nav-links a.education:before {
  background-image: url("../img/education.png");
}
nav.nav-links a.experience:before {
  background-image: url("../img/experience.png");
}
nav.nav-links a.skills:before {
  background-image: url("../img/skills.png");
}
footer {
  padding: 11%;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
@media only screen and (max-width: 480px) {
  footer {
    position: fixed;
    bottom: 0;
    left: 0;
    padding: 5%;
    width: 100%;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    background-color: #fafafa;
    z-index: 1000;
  }
}
footer ul.contact {
  margin: 0;
  padding: 0;
  list-style: none;
}
footer ul.contact a {
  position: relative;
  display: inline-block;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  padding: 0.7em 0 0 2rem;
  text-decoration: none;
  color: #646261;
}
footer ul.contact a:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0.5rem;
  width: 1.5rem;
  height: 1.5rem;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
}
footer ul.contact a.email:before {
  background-image: url("../img/email.png");
}
footer ul.contact a.linkedin:before {
  background-image: url("../img/linkedin.png");
}
footer ul.contact a.github:before {
  background-image: url("../img/github.png");
}
footer ul.contact a.stackoverflow:before {
  background-image: url("../img/stackoverflow.png");
  top: 0;
  width: 2rem;
  height: 2rem;
}
.circle {
  padding-left: 0;
  margin-left: -5px;
  list-style: none;
}
.circle li {
  display: inline-block;
  padding: 0.3rem 2rem 0.3rem 30px;
  position: relative;
  line-height: 0.5;
}
.circle li:before {
  content: "";
  position: absolute;
  left: 5px;
  width: 20px;
  height: 20px;
  bottom: 0px;
  background-image: url("../img/circles.png");
  background-repeat: no-repeat;
}
.circle li.worked:before {
  background-position: 0 -23px;
}
.circle li.visited:before {
  background-position: 0 0;
}
.sidebar {
  background-color: #fafafa;
  opacity: 1;
  -ms-filter: none;
  filter: none;
}
.profile {
  background-color: #f0f0f0;
  text-align: center;
}
.profile h1 {
  text-transform: uppercase;
  margin-bottom: 0;
}
.profile h2.title {
  font-family: 'Noto Sans', sans-serif;
  margin-top: 0.1rem;
}
.profile .avatar {
  position: relative;
}
.profile .avatar img {
  width: 60%;
  border-radius: 50%;
  margin: 5rem 18% 2rem;
}
.profile .avatar .flag {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 14%;
  border: 0.4rem solid #f0f0f0;
}
.profile .location {
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-weight: normal;
  padding-left: 1.5rem;
  font-style: italic;
  display: inline-block;
  margin-top: 1rem;
  font-weight: 100;
}
.profile .location:before {
  content: "";
  position: absolute;
  left: 0;
  width: 1rem;
  height: 1.5rem;
  background-image: url("../img/location.png");
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  background-size: contain;
}
.profile .boxes {
  lost-utility: clearfix;
  border-top: 1px solid #dad9da;
  border-bottom: 1px solid #dad9da;
  margin-top: 20px;
}
.profile .boxes .box {
  padding: 0.7rem;
  -webkit-transition: 200ms;
  -moz-transition: 200ms;
  -o-transition: 200ms;
  -ms-transition: 200ms;
  transition: 200ms;
  lost-column: 1/3 3 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border-right: 1px solid #dad9da;
  text-transform: uppercase;
  line-height: 5rem;
  text-align: center;
}
.profile .boxes .box span {
  display: inline-block;
  vertical-align: middle;
  line-height: normal;
}
.profile .boxes .box span strong {
  font-weight: bold;
  font-size: 1.8rem;
}
.profile .boxes .box:last-child {
  border-right: none;
}
